import uniqueId from 'lodash/uniqueId';
import isNil from 'lodash/isNil';
import get from 'lodash/get';
import { TreeStore } from './tree-store';
import {
  TreeNodeValue,
  TreeNodeState,
  TypeIdMap,
  TypeTreeItem,
  TypeSettingOptions,
  TypeTreeNodeModel,
  TypeTreeNodeData,
} from './types';
import {
  createNodeModel,
  updateNodeModel,
} from './tree-node-model';

const { hasOwnProperty } = Object.prototype;

const defaultStatus = {
  expandMutex: false,
  activable: false,
  checkable: false,
  disabled: false,
  draggable: false,
  loading: false,
};

// vm ÂºÄÂ§¥‰∏∫ËßÜÂõæÂ±ûÊÄßÔºå‰∏çÂèØ‰ª•Â§ñÈÉ®ËÆæÁΩÆ
// Áî®‰∫éËß¶ÂèëËßÜÂõæÊõ¥Êñ∞
export class TreeNode {
  // ËäÇÁÇπÈö∂Â±ûÁöÑÊ†ëÂÆû‰æã
  public tree: TreeStore;

  // ËäÇÁÇπ id ÔºåÂîØ‰∏ÄÊ†áÂøó
  public value: string;

  // ËäÇÁÇπÊñáÊú¨
  public label: string;

  // ËäÇÁÇπÊï∞ÊçÆ
  public data: TypeTreeNodeData;

  // Áà∂ËäÇÁÇπ
  public parent: TreeNode;

  // Â≠êËäÇÁÇπÂàóË°®
  public children: TreeNode[] | boolean;

  // Êö¥Èú≤ÁöÑ treeNodeModelÔºåËøô‰∏™ÂØπË±°ÁöÑÂ±ûÊÄßÂíå api Êèê‰æõÁªôÁî®Êà∑‰ΩøÁî®
  public model: TypeTreeNodeModel;

  // ÊòØÂê¶‰∏∫Âè∂ËäÇÁÇπ
  public vmIsLeaf: boolean;

  // ÊòØÂê¶‰∏∫Â≠êËäÇÁÇπ‰∏≠ÁöÑÁ¨¨‰∏Ä‰∏™
  public vmIsFirst: boolean;

  // ÊòØÂê¶‰∏∫Â≠êËäÇÁÇπ‰∏≠ÁöÑÊúÄÂêé
  public vmIsLast: boolean;

  // ËäÇÁÇπÊòØÂê¶ÊòØÁªèËøáËøáÊª§Ââ©‰∏ãÁöÑ
  public vmIsRest: boolean;

  // ËäÇÁÇπÊòØÂê¶Â±ïÁ§∫‰∏∫ÈîÅÂÆöÁä∂ÊÄÅ
  public vmIsLocked: boolean;

  // ËäÇÁÇπÂú®ËßÜÂõæ‰∏äÂÆûÈôÖÁöÑÂ±ïÂºÄÁä∂ÊÄÅ
  public expanded: boolean;

  // Â±ïÂºÄÊó∂ÊòØÂê¶Êî∂Ëµ∑ÂêåÁ∫ßËäÇÁÇπÔºåÂØπÂ≠êËäÇÁÇπÁîüÊïà
  public expandMutex: boolean;

  // ËäÇÁÇπÂú®ËßÜÂõæ‰∏äÂÆûÈôÖÁöÑÊøÄÊ¥ªÁä∂ÊÄÅ
  public actived: boolean;

  // ÊòØÂê¶ÂèØÊøÄÊ¥ª
  public activable: boolean;

  // ÊòØÂê¶ÂèØÈÄâ‰∏≠
  public checkable: boolean;

  // ÊòØÂê¶ÂèØÈÄâ‰∏≠ÁöÑËßÜÂõæÂëàÁé∞
  public vmCheckable: boolean;

  // ËäÇÁÇπÂú®ËßÜÂõæ‰∏äÂÆûÈôÖÁöÑÈÄâ‰∏≠ÊÄÅ
  public checked: boolean;

  // ËäÇÁÇπÂÆûÈôÖÊòØÂê¶‰∏∫ÂçäÈÄâÁä∂ÊÄÅ
  public indeterminate: boolean;

  // ËäÇÁÇπÊòØÂê¶Â∑≤Á¶ÅÁî®
  public disabled: boolean;

  // ËäÇÁÇπÊòØÂê¶ÂèØÊãñÂä®
  public draggable: boolean;

  // ËäÇÁÇπÊòØÂê¶ÂèØËßÜ
  public visible: boolean;

  // ËäÇÁÇπÂú®Ê†ë‰∏≠ÁöÑÂ±ÇÁ∫ß
  public level: number;

  // ËäÇÁÇπÊòØÂê¶Ê≠£Âú®Âä†ËΩΩÊï∞ÊçÆ
  public loading: boolean;

  public constructor(
    tree: TreeStore,
    data?: TypeTreeNodeData,
    parent?: TreeNode,
  ) {
    this.data = data;
    this.tree = tree;

    const config = tree.config || {};
    const prefix = config.prefix || 't';
    const keys = get(tree, 'config.keys') || {};
    const propChildren = keys.children || 'children';
    const propLabel = keys.label || 'label';
    const propValue = keys.value || 'value';

    this.model = null;
    this.children = null;
    this.vmCheckable = false;
    this.vmIsLeaf = false;
    this.vmIsFirst = false;
    this.vmIsLast = false;
    this.vmIsRest = true;
    this.vmIsLocked = false;

    const spec = {
      ...defaultStatus,
      actived: false,
      expanded: false,
      checked: false,
      ...data,
    };
    const children = spec[propChildren];

    this.set(spec);
    this.label = spec[propLabel] || '';
    this.value = isNil(spec[propValue]) ? uniqueId(prefix) : spec[propValue];
    this.tree.nodeMap.set(this.value, this);

    if (parent && parent instanceof TreeNode) {
      this.parent = parent;
    } else {
      this.parent = null;
    }

    // Â≠êËäÇÁÇπ‰∏∫ true ÁöÑÁä∂ÊÄÅÈÄªËæëÈúÄË¶ÅÊîæÂà∞Áä∂ÊÄÅËÆ°ÁÆó‰πãÂâç
    // ÂàùÂßãÂåñÂä†ËΩΩÈÄªËæëÈúÄË¶Å‰æùÊçÆËøô‰∏™Êù•ËøõË°å
    if (children === true) {
      this.children = children;
    }

    // ÂàùÂßãÂåñÁä∂ÊÄÅËÆ°ÁÆó
    this.level = 0;
    this.visible = true;

    this.actived = spec.actived;
    this.initActived();

    this.expanded = spec.expanded;
    this.initExpanded();

    this.checked = spec.checked;
    this.initChecked();

    this.update();
    tree.reflow(this);

    // ËøôÈáåÁöÑÂ≠êËäÇÁÇπÂä†ËΩΩÈÄªËæë‰∏çËÉΩÊîæÂà∞Áä∂ÊÄÅËÆ°ÁÆó‰πãÂâç
    // Âõ†‰∏∫Â≠êËäÇÁÇπÁä∂ÊÄÅËÆ°ÁÆó‰æùËµñÁà∂ËäÇÁÇπÁä∂ÊÄÅ
    if (Array.isArray(children)) {
      this.append(children);
    } else if (children === true && !config.lazy) {
      this.loadChildren();
    }

    // checked Áä∂ÊÄÅ‰æùËµñ‰∫éÂ≠êËäÇÁÇπÁä∂ÊÄÅ
    // Âõ†Ê≠§ÂàùÂßãÂåñÁä∂ÊÄÅÊîæÂà∞Â≠êËäÇÁÇπÊèíÂÖ•‰πãÂêé
    this.checked = false;
    this.indeterminate = false;
    this.updateChecked();
  }

  /* ------ Áä∂ÊÄÅÂàùÂßãÂåñ ------ */

  // ÂàùÂßãÂåñÈÄâ‰∏≠ÊÄÅ
  public initChecked() {
    const { tree, value, parent } = this;
    const { checkStrictly } = tree.config;
    let { checked } = this;
    checked = parent?.isChecked();
    if (checked && !checkStrictly) {
      tree.checkedMap.set(value, true);
    }
    this.checked = checked;
  }

  // ÂàùÂßãÂåñËäÇÁÇπÂ±ïÂºÄÁä∂ÊÄÅ
  public initExpanded(): void {
    const { tree } = this;
    let { expanded } = this;
    const { config } = tree;
    if (
      typeof config.expandLevel === 'number'
      && this.getLevel() < config.expandLevel
    ) {
      tree.expandedMap.set(this.value, true);
      expanded = true;
    }
    if (config.expandAll) {
      expanded = true;
    }
    if (this.children === true && config.lazy) {
      expanded = false;
    }
    if (expanded) {
      tree.expandedMap.set(this.value, true);
    } else {
      tree.expandedMap.delete(this.value);
    }
    this.expanded = expanded;
  }

  // ÂàùÂßãÂåñÈ´ò‰∫ÆÁä∂ÊÄÅ
  public initActived(): void {
    const { tree, actived } = this;
    if (actived) {
      tree.activedMap.set(this.value, true);
    }
  }

  /* ------ ËäÇÁÇπÊìç‰Ωú ------ */

  // ËøΩÂä†Êï∞ÊçÆ
  public append(data: TypeTreeNodeData | TypeTreeNodeData[]): void {
    const list = [];
    if (!Array.isArray(data)) {
      list.push(data);
    } else {
      list.push(...data);
    }
    if (list.length <= 0) {
      return;
    }
    if (!Array.isArray(this.children)) {
      this.children = [];
    }
    const { children, tree } = this;
    list.forEach((item) => {
      let node = null;
      if (item instanceof TreeNode) {
        node = item;
        node.appendTo(this.tree, this);
      } else {
        node = new TreeNode(this.tree, item, this);
        children.push(node);
      }
    });
    tree.reflow(this);
    this.updateRelated();
  }

  // Â∞ÜÂΩìÂâçËäÇÁÇπËøΩÂä†Âà∞Êüê‰∏™Áà∂ËäÇÁÇπÁöÑÂ≠êËäÇÁÇπÂàóË°®‰∏≠
  public appendTo(
    tree: TreeStore,
    parent?: TreeNode,
    index?: number,
  ): void {
    const parentNode = parent;
    let targetIndex = -1;
    if (typeof index === 'number') {
      targetIndex = index;
    }

    const targetParents = parentNode?.getParents() || [];
    const includeCurrent = targetParents.some((pnode) => pnode === this);
    if (includeCurrent) {
      throw new Error('Êó†Ê≥ïÂ∞ÜÁà∂ËäÇÁÇπÊèíÂÖ•Âà∞Â≠êËäÇÁÇπ');
    }

    if (Array.isArray(parentNode?.children)) {
      const targetPosNode = parentNode?.children[targetIndex];
      if (targetPosNode && targetPosNode === this) {
        // Êó†ÈúÄÂ∞ÜËäÇÁÇπÊèíÂÖ•Âà∞Âéü‰ΩçÁΩÆ
        return;
      }
    }

    // ÂÖàË¶ÅÂèñÂæó siblings
    // Âõ†‰∏∫Ë¶ÅÂ∫îÂØπËäÇÁÇπÂú®Âêå‰∏Ä‰∏™ siblings ‰∏≠ÂèòÊç¢‰ΩçÁΩÆÁöÑÊÉÖÂÜµ
    let siblings = null;
    if (parentNode instanceof TreeNode) {
      if (!Array.isArray(parentNode?.children)) {
        parentNode.children = [];
      }
      siblings = parent.children;
    } else {
      siblings = tree.children;
    }

    if (!Array.isArray(siblings)) {
      throw new Error('Êó†Ê≥ïÊèíÂÖ•Âà∞ÁõÆÊ†á‰ΩçÁΩÆÔºåÂèØÊèíÂÖ•ÁöÑËäÇÁÇπÂàóË°®‰∏çÂ≠òÂú®');
    }

    const prevLength = siblings.length;
    const prevIndex = this.getIndex();

    this.remove();

    if (typeof index === 'number') {
      let targetIndex = index;
      if (parentNode === this.parent) {
        // ÂâçÁΩÆËäÇÁÇπË¢´ÊãîÂá∫ÂêéÂÜçÊèíÂÖ•Âà∞Âêå‰∏Ä‰∏™ siblings Êó∂Ôºå‰ºöÂºïËµ∑ÁõÆÊ†á index ÁöÑÂèòÂåñ
        // Âõ†Ê≠§Ë¶ÅÁõ∏Â∫îÁöÑÂèòÊõ¥ÊèíÂÖ•‰ΩçÁΩÆ
        // ÂêéÁΩÆËäÇÁÇπË¢´ÊãîÂá∫Êó∂ÔºåÁõÆÊ†á index ÊòØ‰∏çÂèòÁöÑ
        const curLength = siblings.length;
        if (
          curLength < prevLength
          && prevIndex <= targetIndex
        ) {
          targetIndex -= 1;
        }
      }
      siblings.splice(targetIndex, 0, this);
    } else {
      siblings.push(this);
    }

    this.parent = parentNode;

    // ÊèíÂÖ•ËäÇÁÇπÂ∫îÂΩìÁªßÊâøÂ±ïÂºÄÁä∂ÊÄÅ
    // ‰ΩÜÂª∫ËÆÆ‰∏çË¶ÅÁªßÊâøÈÄâ‰∏≠Áä∂ÊÄÅÂíåÈ´ò‰∫ÆÁä∂ÊÄÅ
    const nodes = this.walk();
    nodes.forEach((item) => {
      const node = item;
      node.tree = tree;
      tree.nodeMap.set(node.value, node);
      if (node.expanded) {
        tree.expandedMap.set(node.value, true);
      }
    });

    const updateNodes = parentNode?.walk() || tree.children.map((item) => item.walk()).flat();
    updateNodes.forEach((node) => {
      node.update();
      node.updateChecked();
    });

    tree.reflow();
  }

  // ÊèíÂÖ•‰∏Ä‰∏™ÂêåÁ∫ßËäÇÁÇπÊï∞ÊçÆ
  public insert(
    item: TypeTreeItem,
    index?: number,
  ): void {
    const { tree, parent } = this;
    const siblings = this.getSiblings();
    let node = null;
    if (item instanceof TreeNode) {
      node = item;
      node.appendTo(tree, parent, index);
    } else if (item) {
      node = new TreeNode(tree, item, parent);
      if (typeof index === 'number') {
        siblings.splice(index, 0, node);
      }
      siblings.forEach((sibling) => {
        sibling.update();
      });
    }
    tree.reflow();
  }

  // Âú®ÂΩìÂâçËäÇÁÇπ‰πãÂâçÊèíÂÖ•ËäÇÁÇπ
  public insertBefore(newData: TypeTreeItem): void {
    const index = this.getIndex();
    this.insert(newData, index);
  }

  // Âú®ÂΩìÂâçËäÇÁÇπ‰πãÂêéÊèíÂÖ•ËäÇÁÇπ
  public insertAfter(newData: TypeTreeItem): void {
    const index = this.getIndex();
    this.insert(newData, index + 1);
  }

  // ‰ªé‰∏Ä‰∏™Ê†ëÁßªÈô§Êú¨ËäÇÁÇπ
  public remove(): void {
    const { tree } = this;

    const nodes = this.walk();
    const siblings = this.getSiblings();
    const index = this.getIndex();
    // ‰ªéÁà∂ËäÇÁÇπÁöÑÂ≠êËäÇÁÇπÂàóË°®‰∏≠ÁßªÈô§Ëá™Â∑±
    // ‰ΩÜ‰∏çË¶ÅÂ∞ÜËá™Â∑±ÁöÑÁà∂ËäÇÁÇπÁßªÈô§ÔºåÈÅøÂÖçÊ∏≤Êüì‰∏éÂà§Êñ≠Â§±Ë¥•
    if (Array.isArray(siblings)) {
      siblings.splice(index, 1);
    }
    // Ê∏ÖÁêÜ‰∏éÊ†ëÁöÑÂÖ≥Á≥ªÔºå‰ΩÜ‰∏çÊ∏ÖÁêÜËá™Ë∫´Áä∂ÊÄÅ
    nodes.forEach((node) => {
      node.clean();
    });
    // ÂêåÁ∫ßËäÇÁÇπÁöÑËøûÁ∫øÁä∂ÊÄÅ‰ºöÂèóÂà∞ÂΩ±Âìç
    siblings.forEach((node) => {
      node.update();
    });
    // Áà∂ËäÇÁÇπÈÄâ‰∏≠ÊÄÅ‰ºöÂèóÂà∞ÂΩ±Âìç
    this.updateParents();
    tree.reflow();
  }

  // Ê∏ÖÈô§Êú¨ËäÇÁÇπ‰∏é‰∏Ä‰∏™Ê†ëÁöÑÂÖ≥Á≥ª
  public clean(): void {
    const { tree, value } = this;
    tree.activedMap.delete(value);
    tree.checkedMap.delete(value);
    tree.expandedMap.delete(value);
    tree.nodeMap.delete(value);
  }

  // ÂºÇÊ≠•Âä†ËΩΩÂ≠êËäÇÁÇπÊï∞ÊçÆ
  public async loadChildren(): Promise<void> {
    const config = get(this, 'tree.config') || {};
    if (this.children === true && !this.loading) {
      if (typeof config.load === 'function') {
        this.loading = true;
        this.update();
        let list = [];
        list = await config.load(this);
        this.tree.emit('load', {
          node: this,
          data: list,
        });
        this.loading = false;
        if (Array.isArray(list) && list.length > 0) {
          this.append(list);
        } else {
          this.children = false;
        }
        this.update();
      }
    }
  }

  // ËÆæÁΩÆÁä∂ÊÄÅ
  public set(item: TreeNodeState): void {
    const { tree } = this;
    const keys = Object.keys(item);
    keys.forEach((key) => {
      if (hasOwnProperty.call(defaultStatus, key) || key === 'label') {
        this[key] = item[key];
      }
    });
    tree.updated(this);
  }

  /* ------ ËäÇÁÇπËé∑Âèñ ------- */

  // Ëé∑ÂèñÂçï‰∏™Áà∂ËäÇÁÇπ
  public getParent(): TreeNode {
    return this.parent;
  }

  // Ëé∑ÂèñÊâÄÊúâÁà∂ËäÇÁÇπ
  public getParents(): TreeNode[] {
    const parents = [];
    let node = this.parent;
    while (node) {
      parents.push(node);
      node = node.parent;
    }
    return parents;
  }

  // Ëé∑ÂèñÂÖÑÂºüËäÇÁÇπÔºåÂåÖÂê´Ëá™Â∑±Âú®ÂÜÖ
  public getSiblings(): TreeNode[] {
    const { parent, tree } = this;
    let list: TreeNode[] = [];
    if (parent) {
      if (Array.isArray(parent.children)) {
        list = parent.children;
      }
    } else if (tree) {
      list = tree.children;
    }
    return list;
  }

  // Ëé∑ÂèñÊ†πËäÇÁÇπ
  public getRoot(): TreeNode {
    const parents = this.getParents();
    return parents[parents.length - 1] || null;
  }

  // Ëé∑ÂèñËäÇÁÇπÂú®Áà∂ËäÇÁÇπÁöÑÂ≠êËäÇÁÇπÂàóË°®‰∏≠ÁöÑ‰ΩçÁΩÆ
  // Â¶ÇÊûúÊ≤°ÊúâÁà∂ËäÇÁÇπÔºåÂàôËé∑ÂèñËäÇÁÇπÂú®Ê†πËäÇÁÇπÂàóË°®ÁöÑ‰ΩçÁΩÆ
  public getIndex(): number {
    const list = this.getSiblings();
    return list.indexOf(this);
  }

  // ËøîÂõûË∑ØÂæÑËäÇÁÇπ
  public getPath(): TreeNode[] {
    const nodes = this.getParents();
    nodes.unshift(this);
    return nodes.reverse();
  }

  // Ëé∑ÂèñËäÇÁÇπÊâÄÂú®Â±ÇÁ∫ß
  public getLevel(): number {
    const parents = this.getParents();
    return parents.length;
  }

  /* ------ ËäÇÁÇπÁä∂ÊÄÅÂà§Êñ≠ ------ */

  // Âà§Êñ≠ËäÇÁÇπÊòØÂê¶Ë¢´ËøáÊª§
  public isRest(): boolean {
    const {
      config,
      filterMap,
    } = this.tree;

    let rest = true;
    if (typeof config.filter === 'function') {
      const nodeModel = this.getModel();
      rest = config.filter(nodeModel);
    }

    if (rest) {
      filterMap.set(this.value, true);
    } else if (filterMap.get(this.value)) {
      filterMap.delete(this.value);
    }

    return rest;
  }

  // Âà§Êñ≠ËäÇÁÇπÊòØÂê¶ÂèØËßÜ
  public isVisible(): boolean {
    const {
      nodeMap,
    } = this.tree;

    let visible = true;

    // ÈîÅÂÆöÁä∂ÊÄÅÔºåÁõ¥Êé•ÂëàÁé∞
    if (this.vmIsLocked) {
      return true;
    }

    // Âú®ÂΩìÂâçÊ†ë‰∏äÔºåÊú™Ë¢´ÁßªÈô§
    if (nodeMap.get(this.value)) {
      // ËäÇÁÇπÊú™Ë¢´ËøáÊª§
      const filterVisible = this.isRest();

      // ÊâÄÊúâÁà∂ËäÇÁÇπÂ±ïÂºÄ
      let expandVisible = true;
      const parents = this.getParents();
      if (parents.length > 0) {
        expandVisible = parents.every((node: TreeNode) => node.isExpanded());
      }

      // ËäÇÁÇπ‰∏∫Êú™Ë¢´ËøáÊª§ËäÇÁÇπÁöÑÁà∂ËäÇÁÇπ
      visible = expandVisible && filterVisible;
    } else {
      visible = false;
    }
    return visible;
  }

  // Âà§Êñ≠ËäÇÁÇπÊòØÂê¶Ë¢´Á¶ÅÁî®
  public isDisabled() {
    if (this.vmIsLocked) return true;
    const treeDisabled = get(this, 'tree.config.disabled');
    return !!(treeDisabled || this.disabled);
  }

  // Âà§Êñ≠ËäÇÁÇπÊòØÂê¶ËÉΩÊãñÊãΩ
  public isDraggable() {
    return !!(get(this, 'tree.config.draggable') || this.draggable);
  }

  // Âà§Êñ≠ËäÇÁÇπÊòØÂê¶ÊîØÊåÅ‰∫íÊñ•Â±ïÂºÄ
  public isExpandMutex() {
    return !!(get(this, 'tree.config.expandMutex') || this.expandMutex);
  }

  // ËäÇÁÇπÂèØÈ´ò‰∫Æ
  public isActivable() {
    return !!(get(this, 'tree.config.activable') || this.activable);
  }

  // ÊòØÂê¶ÂèØÈÄâ
  public isCheckable() {
    return !!(get(this, 'tree.config.checkable') || this.checkable);
  }

  // Ê£ÄÊü•ËäÇÁÇπÊòØÂê¶Ë¢´ÊøÄÊ¥ª
  public isActived(map?: Map<string, boolean>): boolean {
    const { tree, value } = this;
    const activedMap = map || tree.activedMap;
    return !!(tree.nodeMap.get(value) && activedMap.get(value));
  }

  // Ê£ÄÊü•ËäÇÁÇπÊòØÂê¶Â∑≤Â±ïÂºÄ
  public isExpanded(map?: Map<string, boolean>): boolean {
    const { tree, value, vmIsLocked } = this;
    if (vmIsLocked) return true;
    const expandedMap = map || tree.expandedMap;
    return !!(tree.nodeMap.get(value) && expandedMap.get(value));
  }

  // ËÆ°ÁÆóÂ±ûÊÄßÔºåÂà§Êñ≠ËäÇÁÇπÊòØÂê¶Ë¢´ÈÄâ‰∏≠
  // map: È¢ÑÊúüÈÄâ‰∏≠È°πmapÔºåÁî®‰∫éËÆ°ÁÆóËäÇÁÇπÂú®È¢ÑÊúüÁéØÂ¢É‰∏≠ÁöÑÈÄâ‰∏≠ÊÄÅ
  public isChecked(map?: TypeIdMap): boolean {
    const { children, tree } = this;
    const { checkStrictly } = tree.config;
    let checked = false;
    const checkedMap = map || tree.checkedMap;
    if (tree.nodeMap.get(this.value)) {
      if (checkedMap.get(this.value)) {
        // Â¶ÇÊûúÂú® checked ËäÇÁÇπÂàóË°®‰∏≠ÔºåÂàôÁõ¥Êé•‰∏∫ true
        checked = true;
      } else if (
        Array.isArray(children)
        && children.length > 0
        && !checkStrictly
      ) {
        // Â¶ÇÊûúÊòØÁà∂ËäÇÁÇπÔºåÈúÄÊ£ÄÊü•ÊâÄÊúâÂ≠êËäÇÁÇπÁä∂ÊÄÅ
        checked = children.every((node) => {
          const childIsChecked = node.isChecked(checkedMap);
          return childIsChecked;
        });
      } else if (!checkStrictly) {
        // ‰ªéÁà∂ËäÇÁÇπÁä∂ÊÄÅÊé®Êñ≠Â≠êËäÇÁÇπÁä∂ÊÄÅ
        // ËøôÈáåÂÜçË∞ÉÁî® isChecked ‰ºöÂØºËá¥Ê≠ªÂæ™ÁéØ
        const parents = this.getParents();
        checked = parents.some((node) => checkedMap.get(node.value));
      }
    }
    return !!checked;
  }

  // ÊòØÂê¶‰∏∫ÂçäÈÄâÁä∂ÊÄÅ
  public isIndeterminate(): boolean {
    const { children, tree } = this;
    const { checkStrictly } = tree.config;
    if (checkStrictly) {
      return false;
    }
    let indeterminate = false;
    if (Array.isArray(children)) {
      // Âè∂ËäÇÁÇπ‰∏çÂ≠òÂú®ÂçäÈÄâÁä∂ÊÄÅ
      let childChecked: null | boolean = null;
      indeterminate = children.some((node: TreeNode) => {
        if (node.isIndeterminate()) {
          // Â≠êËäÇÁÇπÊúâ‰ªªÊÑè‰∏Ä‰∏™ÂçäÈÄâÔºåÂàôÂÖ∂‰∏∫ÂçäÈÄâÁä∂ÊÄÅ
          return true;
        }
        if (childChecked === null) {
          childChecked = node.isChecked();
        }
        if (childChecked !== node.isChecked()) {
          // Â≠êËäÇÁÇπÈÄâ‰∏≠Áä∂ÊÄÅ‰∏ç‰∏ÄËá¥ÔºåÂàôÂÖ∂‰∏∫ÂçäÈÄâÁä∂ÊÄÅ
          return true;
        }
        return false;
      });
    }
    return indeterminate;
  }

  public isFirst(): boolean {
    const siblings = this.getSiblings();
    return siblings[0] === this;
  }

  public isLast(): boolean {
    const siblings = this.getSiblings();
    return siblings[siblings.length - 1] === this;
  }

  // ÊòØÂè∂ËäÇÁÇπ
  public isLeaf(): boolean {
    let isLeaf = false;
    if (Array.isArray(this.children)) {
      isLeaf = this.children.length <= 0;
    } else {
      isLeaf = !this.children;
    }
    return isLeaf;
  }

  /* ------ ËäÇÁÇπÁä∂ÊÄÅÂàáÊç¢ ------ */

  // ÈîÅÂÆöËäÇÁÇπ
  // ÊêúÁ¥¢ËøáÊª§ËäÇÁÇπÊó∂ÔºåË∑ØÂæÑËäÇÁÇπÈúÄË¶ÅÂõ∫ÂÆöÂëàÁé∞ÔºåËßÜÂÖ∂‰∏∫ÈîÅÂÆöÊÄÅ
  public lock(lockState: boolean): void {
    this.vmIsLocked = lockState;
    this.expanded = this.isExpanded();
    this.visible = this.isVisible();
  }

  // ËäÇÁÇπÂ±ïÂºÄÂÖ≥Èó≠ÂêéÈúÄË¶ÅË∞ÉÁî®ÁöÑÁä∂ÊÄÅÊ£ÄÊü•ÂáΩÊï∞
  public afterExpanded(): void {
    this.update();
    // ËäÇÁÇπÂ±ïÂºÄÊó∂Ê£ÄÊü•Âª∂ËøüÂä†ËΩΩÁöÑÊï∞ÊçÆ
    if (this.expanded && this.children === true) {
      this.loadChildren();
    }
  }

  // Â±ïÂºÄÊàñËÄÖÂÖ≥Èó≠ËäÇÁÇπ
  public toggleExpanded(): TreeNodeValue[] {
    return this.setExpanded(!this.isExpanded());
  }

  // ËÆæÁΩÆËäÇÁÇπÂ±ïÂºÄÁä∂ÊÄÅ
  public setExpanded(expanded: boolean, opts?: TypeSettingOptions): TreeNodeValue[] {
    const { tree } = this;
    const options = {
      directly: false,
      ...opts,
    };

    let map = tree.expandedMap;
    if (!options.directly) {
      map = new Map(tree.expandedMap);
    }

    // ÊâãÈ£éÁê¥ÊïàÊûúÔºåÂÖàÊäòÂè†ÂêåÁ∫ßËäÇÁÇπ
    if (expanded) {
      const shouldExpandNodes = [];
      shouldExpandNodes.push(this);
      if (get(tree, 'config.expandParent')) {
        this.getParents().forEach((node) => {
          shouldExpandNodes.push(node);
        });
      }
      shouldExpandNodes.forEach((node) => {
        let isExpandMutex = false;
        if (node.parent) {
          isExpandMutex = node.parent.isExpandMutex();
        } else {
          isExpandMutex = tree?.config?.expandMutex;
        }
        if (isExpandMutex) {
          const siblings = node.getSiblings();
          siblings.forEach((snode) => {
            map.delete(snode.value);
          });
        }
        map.set(node.value, true);
      });
    } else {
      map.delete(this.value);
    }

    if (options.directly) {
      this.afterExpanded();
      this.update();
      this.updateChildren();
    }

    return tree.getExpanded(map);
  }

  // ÂàáÊç¢ËäÇÁÇπÊøÄÊ¥ªÊÄÅ
  public toggleActived(): TreeNodeValue[] {
    return this.setActived(!this.isActived());
  }

  // ËÆæÁΩÆËäÇÁÇπÊøÄÊ¥ªÊÄÅ
  public setActived(actived: boolean, opts?: TypeSettingOptions): TreeNodeValue[] {
    const { tree } = this;
    const options = {
      directly: false,
      ...opts,
    };
    const config = tree.config || {};
    let map = tree.activedMap;
    if (!options.directly) {
      map = new Map(tree.activedMap);
    }
    if (this.isActivable()) {
      if (actived) {
        if (!config.activeMultiple) {
          map.clear();
        }
        map.set(this.value, true);
      } else {
        map.delete(this.value);
      }
    }
    if (options.directly) {
      this.update();
    }
    return tree.getActived(map);
  }

  // ÂàáÊç¢ÈÄâ‰∏≠ÊÄÅ
  public toggleChecked(): TreeNodeValue[] {
    return this.setChecked(!this.isChecked());
  }

  // Êõ¥Êñ∞Âçï‰∏™ËäÇÁÇπÁöÑÈÄâ‰∏≠ÊÄÅ
  // ËøîÂõûÊ†ëÈÄâ‰∏≠ÂàóË°®
  public setChecked(checked: boolean, opts?: TypeSettingOptions): TreeNodeValue[] {
    const { tree } = this;
    const config = tree.config || {};
    const options = {
      directly: false,
      ...opts,
    };
    let map = tree.checkedMap;
    if (!options.directly) {
      map = new Map(tree.checkedMap);
    }
    if (this.isCheckable() && checked !== this.isChecked()) {
      if (config.checkStrictly) {
        if (checked) {
          map.set(this.value, true);
        } else {
          map.delete(this.value);
        }
      } else {
        const children = this.walk();
        // Â≠êËäÇÁÇπÁöÑÈ¢ÑÊúüÈÄâ‰∏≠ÊÄÅ‰∏éÂΩìÂâçËäÇÁÇπÂêåÊ≠•
        children.forEach((node) => {
          if (checked) {
            map.set(node.value, true);
          } else {
            map.delete(node.value);
          }
        });
        // Ê∂àÈô§ÂÖ®ÈÉ®Áà∂ËäÇÁÇπÁöÑÈ¢ÑÊúüÈÄâ‰∏≠ÊÄÅ
        // Áà∂ËäÇÁÇπÁöÑÈ¢ÑÊúüÈÄâ‰∏≠ÊÄÅÂ∞ÜÈÄöËøáËÆ°ÁÆóÂæóÂá∫
        const parents = this.getParents();
        parents.forEach((node) => {
          map.delete(node.value);
        });
      }
    }
    if (options.directly) {
      if (config.checkStrictly) {
        this.updateChecked();
      } else {
        const relatedNodes = tree.getRelatedNodes([this.value]);
        relatedNodes.forEach((node) => {
          node.updateChecked();
        });
      }
    }
    return tree.getChecked(map);
  }

  /* ------ ËäÇÁÇπÁä∂ÊÄÅÊõ¥Êñ∞ ------ */

  // Êõ¥Êñ∞ËäÇÁÇπÁä∂ÊÄÅ
  public update(): void {
    this.level = this.getLevel();
    this.actived = this.isActived();
    this.expanded = this.isExpanded();
    this.vmCheckable = this.isCheckable();
    this.visible = this.isVisible();
    this.vmIsRest = this.isRest();
    this.vmIsFirst = this.isFirst();
    this.vmIsLast = this.isLast();
    this.vmIsLeaf = this.isLeaf();
    this.tree.updated(this);
  }

  // Êõ¥Êñ∞ÈÄâ‰∏≠ÊÄÅÂ±ûÊÄßÂÄº
  public updateChecked(isFromValueChange?: boolean): void {
    const { tree } = this;
    this.vmCheckable = this.isCheckable();
    if (this.vmCheckable && (!this.disabled || isFromValueChange)) {
      this.checked = this.isChecked();
      if (this.checked) {
        tree.checkedMap.set(this.value, true);
      }
      this.indeterminate = this.isIndeterminate();
      tree.updated(this);
    }
  }

  // Êõ¥Êñ∞ÊâÄÊúâÂ≠êËäÇÁÇπÁä∂ÊÄÅ
  // Ê≥®ÊÑè:‰∏çÂåÖÂê´Ëá™Â∑±
  public updateChildren(): void {
    const { children } = this;
    if (Array.isArray(children)) {
      children.forEach((node) => {
        node.update();
        node.updateChecked();
        node.updateChildren();
      });
    }
  }

  // Áà∂ËäÇÁÇπÁä∂ÊÄÅÊõ¥Êñ∞
  // Ê≥®ÊÑè:‰∏çÂåÖÂê´Ëá™Â∑±
  public updateParents(): void {
    const { parent } = this;
    if (parent) {
      parent.update();
      parent.updateChecked();
      parent.updateParents();
    }
  }

  // Êõ¥Êñ∞‰∏ä‰∏ãÊ∏∏Áõ∏ÂÖ≥ËäÇÁÇπ
  public updateRelated() {
    const { tree } = this;
    const relatedNodes = tree.getRelatedNodes([this.value]);
    relatedNodes.forEach((node) => {
      node.update();
      node.updateChecked();
    });
    tree.reflow();
  }

  /* ------ ËäÇÁÇπÈÅçÂéÜ ------ */

  // Ëé∑ÂèñÂåÖÂê´Ëá™Â∑±Âú®ÂÜÖÊâÄÊúâÁöÑÂ≠êËäÇÁÇπ
  public walk(): TreeNode[] {
    const { children } = this;
    let list: TreeNode[] = [];
    list.push(this);
    if (Array.isArray(children) && children.length > 0) {
      children.forEach((node) => {
        list = list.concat(node.walk());
      });
    }
    return list;
  }

  // TreeNode ÂØπË±° => TypeTreeNodeModel ÂØπË±°
  // Áî®‰∫é treeNode ÂØπÂ§ñÊö¥Èú≤ÁöÑ api
  // ÁªèËøáÂ∞ÅË£ÖÁöÑÂØπË±°ÔºåÂáèÂ∞ë‰∫ÜÂØπÂ§ñÊö¥Èú≤ÁöÑ apiÔºåÂà©‰∫é‰ª£Á†ÅÈáçÊûÑ
  public getModel(): TypeTreeNodeModel {
    let { model } = this;
    if (!model) {
      model = createNodeModel(this);
      this.model = model;
    }
    updateNodeModel(model, this);
    return model;
  }
}

export default TreeNode;
