  /** Ë∑≥ËΩ¨Âú∞ÂùÄ„ÄÇhref Â≠òÂú®Êó∂ÔºåÊåâÈíÆÊ†áÁ≠æÈªòËÆ§‰ΩøÁî® `<a>` Ê∏≤ÊüìÔºõÂ¶ÇÊûúÊåáÂÆö‰∫Ü `tag` Âàô‰ΩøÁî®ÊåáÂÆöÁöÑÊ†áÁ≠æÊ∏≤Êüì */
  href: String,
  /** ÊåâÈíÆÂÜÖÈÉ®ÂõæÊ†áÔºåÂèØÂÆåÂÖ®Ëá™ÂÆö‰πâ */
