/* eslint-disable */
import { ref, toRefs, reactive, onMounted, computed, watch, nextTick } from 'vue';

// ËôöÊãüÊªöÂä®HooksÁöÑÂÆåÊï¥ÂÆûÁé∞ÔºåÂè™ÊâÄ‰ª•Â∞ÅË£ÖÊàêhooksÔºå‰∏ªË¶ÅÊòØ‰∏∫‰∫ÜÊñπ‰æøË∑üÂÖ∂‰ªñÁªÑ‰ª∂Êê≠ÈÖç‰ΩøÁî®ÔºåÊØîÂ¶ÇËØ¥Ë°®Ê†ºÊàñËÄÖ‰∏ãÊãâÊ°Ü
const useVirtualScroll = ({
  data,
  container,
  fixedHeight = false,
  lineHeight = 30,
  bufferSize = 20,
  threshold = 100,
}: {
  data: any;
  container: any;
  fixedHeight: boolean;
  lineHeight: number;
  bufferSize: number;
  threshold: number;
}) => {
  const state = reactive({
    visibleData: [],
    cachedHeight: [],
    cachedScrollY: [],
  });
  const isVirtual = computed(() => data.value.length > threshold);
  const updateId = ref(0);
  const trs = new Map(); // ÂΩìÂâçÂ±ïÁ§∫ÁöÑË°åÂÖÉÁ¥†ÂíåÊï∞ÊçÆ

  let visibleCount = 0; // ÂèØËßÅÁöÑËäÇÁÇπÊï∞Èáè
  let beforeScrollTop = 0; // ‰∏ä‰∏ÄÊ¨°ÁöÑÊªöÂä®Êù°‰ΩçÁΩÆ
  let index = 0; // ÂÅèÁßªË°åÊï∞
  let offset = 0; // Â∞ë‰∫é‰∏ÄË°åË°åÈ´òÁöÑÂÅèÁßªÈáè
  let start = 0; // Á¨¨‰∏ÄÊù°ÊòæÁ§∫ÁöÑË°å
  let last = 0; // ÊúÄÂêé‰∏ÄÊù°ÊòæÁ§∫ÁöÑË°å
  // let revising = false; // ÊòØÂê¶Ê≠£Âú®‰øÆÊ≠£ÊªöÂä®Êù°

  const reset = () => {
    data.value.forEach((item: any, i: number) => {
      item.$index = i;
      if (fixedHeight) {
        state.cachedScrollY[i] = i * lineHeight;
      }
    });
    if (!fixedHeight) {
      state.cachedScrollY[data.value.length - 1] = undefined; // ÂàùÂßãÂåñcachedScrollYÊï∞ÁªÑÁöÑÈïøÂ∫¶
    }
  };
  reset();

  // ËÆ°ÁÆóËôöÊãüÊªöÂä®ÂàóË°®ÊÄªÈ´òÂ∫¶ÔºåÈúÄË¶ÅÂä®ÊÄÅ‰øÆÊ≠£
  const scrollHeight = computed(() => {
    const { cachedHeight } = state;
    const { length } = cachedHeight;
    if (length) {
      const maxScrollY = cachedHeight.reduce((sum, v) => sum + v || lineHeight, 0); // ÂΩìÂâçÊÄªÈ´òÂ∫¶
      if (cachedHeight.length === data.value.length) {
        return maxScrollY;
      }
      const average = maxScrollY / cachedHeight.length; // Âπ≥ÂùáÈ´òÂ∫¶
      return maxScrollY + (data.value.length - cachedHeight.length) * average; // È¢Ñ‰º∞ÊÄªÈ´òÂ∫¶
    }
    return isVirtual.value ? data.value.length * lineHeight : 0;
  });
  const translateY = computed(() => {
    const { visibleData } = state;
    const firstRow = visibleData[0];
    if (firstRow) {
      // ‰øÆÂ§çÂè™Êúâ‰∏Ä‰∏™ÂÖÉÁ¥†Êó∂Â≠òÂú®ÂÅèÁßªÁöÑÈóÆÈ¢ò
      return visibleData.length === 1 ? 0 : state.cachedScrollY[firstRow.$index];
    }
    return 0;
  });

  /** Á¨¨‰∫åÁßçÂÆûÁé∞Ôºå‰ΩøÁî®watchÁõëÂê¨cachedScrollY‰πüÂèØ */
  // const translateY = ref(0);
  // watch(() => state.cachedScrollY, () => {
  //   const { visibleData } = state;
  //   const firstRow = visibleData[0];
  //   if (firstRow) {
  //     // ‰øÆÂ§çÂè™Êúâ‰∏Ä‰∏™ÂÖÉÁ¥†Êó∂Â≠òÂú®ÂÅèÁßªÁöÑÈóÆÈ¢ò
  //     translateY.value = visibleData.length === 1 ? 0 : state.cachedScrollY[firstRow.$index];
  //     return;
  //   }
  //   translateY.value = 0;
  // });

  // Êõ¥Êñ∞ÂèØËßÜÂå∫ÂüüÁöÑËäÇÁÇπÊï∞ÊçÆ
  const updateVisibleData = () => {
    last = Math.min(start + visibleCount + bufferSize * 2, data.value.length);
    state.visibleData = data.value.slice(start, last);
  };
  // ËÆ°ÁÆóÊØèË°åÂØπÂ∫îÁöÑscrollTopÂÄº
  const calculateScrollY = () => {
    const anchorDom = trs.get(index); // Ëé∑ÂèñÈîöÁÇπÂÖÉÁ¥†
    if (!anchorDom) {
      return; // Âø´ÈÄüË∞ÉÊï¥È´òÂ∫¶Êó∂ÔºåÊñ∞ÁöÑÂÖÉÁ¥†ÂèØËÉΩÊù•‰∏çÂèäÂä†ËΩΩÔºåÊöÇÊó∂Ë∑≥ËøáÊõ¥Êñ∞
    }
    const anchorDomHeight = anchorDom?.getBoundingClientRect()?.height; // Ëé∑ÂèñÈîöÁÇπÂÖÉÁ¥†ÁöÑÈ´ò
    state.cachedScrollY[index] = container.value.scrollTop - offset; // ÈîöÁÇπÂÖÉÁ¥†scrollY= ÂÆπÂô®ÊªöÂä®È´òÂ∫¶ - ÈîöÁÇπÂÖÉÁ¥†ÁöÑoffset
    state.cachedHeight[index] = anchorDomHeight;

    for (let i = index + 1; i <= state.visibleData[state.visibleData.length - 1].$index; i++) {
      // ËÆ°ÁÆóÈîöÁÇπÂêéÈù¢ÁöÑÂÖÉÁ¥†scrollY
      const tr = trs.get(i);
      const { height } = tr?.getBoundingClientRect() || {};
      state.cachedHeight[i] = height;
      const scrollY = state.cachedScrollY[i - 1] + state.cachedHeight[i - 1]; // ÂΩìÂâçÂÖÉÁ¥†ÁöÑy ÊòØÂâç‰∏Ä‰∏™ÂÖÉÁ¥†ÁöÑy+Ââç‰∏Ä‰∏™ÂÖÉÁ¥†È´òÂ∫¶
      // state.cachedScrollY[i] = scrollY;
      state.cachedScrollY.splice(i, 1, scrollY); // ÂÖºÂÆπvue2ÁöÑcomposition api
    }

    for (let i = index - 1; i >= state.visibleData[0].$index; i--) {
      const tr = trs.get(i);
      const { height } = tr?.getBoundingClientRect() || {};
      state.cachedHeight[i] = height;
      const scrollY = state.cachedScrollY[i + 1] - state.cachedHeight[i]; // ÂΩìÂâçÂÖÉÁ¥†ÁöÑyÊòØ‰∏ã‰∏Ä‰∏™ÂÖÉÁ¥†y - ÂΩìÂâçÂÖÉÁ¥†È´òÂ∫¶
      // state.cachedScrollY[i] = scrollY;
      state.cachedScrollY.splice(i, 1, scrollY);
    }
    if (state.cachedScrollY[0] > 0) {
      // ‰øÆÊ≠£ÊªöÂä®ËøáÂø´Êó∂ÔºåÊªöÂä®Âà∞È°∂ÈÉ®Êó∂ÔºåÊªöÂä®Êù°Â§ö‰ΩôÁöÑÈóÆÈ¢ò
      // revising = true;
      const distance = state.cachedScrollY[0]; // Á¨¨‰∏Ä‰∏™ÂÖÉÁ¥†scrollYÂç≥‰∏∫Â§öÂá∫ÁöÑÈáè
      const length = Math.min(last, data.value.length);
      for (let i = 0; i < length; i++) {
        // state.cachedScrollY[i] -= distance;
        state.cachedScrollY.splice(i, 1, state.cachedScrollY[i] - distance);
      }

      const scrollTop = state.cachedScrollY[index - 1] ? state.cachedScrollY[index - 1] + offset : offset;
      container.value.scrollTop = scrollTop;
      beforeScrollTop = scrollTop;
      // revising = false;
    }
    // ‰øÆÊ≠£ÊãñÂä®ËøáÂø´Êó∂ÔºåÊªöÂä®Âà∞È°∂Á´ØÊó∂ÔºåÊªöÂä®Êù°‰∏çË∂≥ÁöÑÂÅèÂ∑Æ
    if (state.cachedScrollY[start] < 0) {
      // revising = true;
      const s = state.cachedHeight.slice(0, Math.max(0, index)).reduce((sum, v) => sum + v, 0) + offset;
      container.value.scrollTop = s;
      beforeScrollTop = s;
      if (s === 0) {
        index = 0;
        offset = 0;
      }
      // revising = false;
    }
    nextTick(() => {
      // setTimeoutÊòØ‰∏∫‰∫Ü‰øùËØÅÂø´ÈÄüÊãñÂä®Âà∞Â∫ïÈÉ®Êó∂Ôºå‰ª•‰∏ãÈÄªËæëËÉΩÂ§üÊ≠£Â∏∏ÊâßË°å
      const { scrollTop, scrollHeight, clientHeight } = container.value;
      if (scrollTop + clientHeight === scrollHeight) {
        // ÊªöÂä®Âà∞Â∫ïÈÉ®Êó∂Ôºå‰øÆÊ≠£Â∫ïÈÉ®ÊúâÁ©∫‰ΩôÁöÑÈóÆÈ¢ò
        // revising = true;
        for (let i = last - 1; i >= start; i--) {
          if (i === last - 1) {
            // state.cachedScrollY[i] = scrollHeight.value - state.cachedHeight[i];
            state.cachedScrollY.splice(i, 1, scrollHeight.value - state.cachedHeight[i]);
          } else {
            // state.cachedScrollY[i] = state.cachedScrollY[i + 1] - state.cachedHeight[i];
            state.cachedScrollY.splice(i, 1, state.cachedScrollY[i + 1] - state.cachedHeight[i]);
          }
        }
        // revising = false;
      }
    });
  };

  // ÊªöÂä®Êó∂Âä®ÊÄÅËÆ°ÁÆóÂíåÊ∏≤Êüì
  const handleScroll = () => {
    if (!isVirtual.value) return;
    // if (revising) {
    //   return false; // ‰øÆÊ≠£ÊªöÂä®Êù°Êó∂ÔºåÊöÇÂÅúÊªöÂä®ÈÄªËæë
    // }
    const { scrollTop } = container.value;
    let distance = scrollTop - beforeScrollTop; // ÊªöÂä®Â∑ÆÂÄº
    beforeScrollTop = scrollTop;
    distance += offset;
    let lastIndex = index;
    // !disatance ÂèØËÉΩ‰∏∫Ê®™ÂêëÊªöÂä®Ôºå‰∏çÂÅö‰ªª‰ΩïËÆ°ÁÆó
    if (!distance) return;
    if (distance >= 0) {
      // Âêë‰∏ãÊªöÂä®
      while (lastIndex < data.value.length && distance > (state.cachedHeight[lastIndex] || lineHeight)) {
        if (!state.cachedHeight[lastIndex]) {
          state.cachedHeight[lastIndex] = lineHeight;
        }
        distance -= state.cachedHeight[lastIndex];
        lastIndex++;
      }
      if (lastIndex >= data.value.length) {
        index = data.value.length - 1;
        offset = 0;
      } else {
        index = lastIndex;
        offset = distance;
      }
      const { clientHeight, scrollHeight } = container.value;
      if (scrollTop + clientHeight === scrollHeight) {
        // ÊªöÂä®Êù°Âà∞Â∫ï‰∫Ü
        index = data.value.length - visibleCount + 1;
        // calculateScrollY();
      }
      if (start <= index - bufferSize) {
        // ËÆ°ÁÆóÁ¨¨‰∏Ä‰∏™ÊåÇËΩΩÂÖÉÁ¥†
        start = Math.min(data.value.length - visibleCount, index - bufferSize);
        if (start < 0) {
          start = 0;
        }
      }
    } else {
      // Âêë‰∏äÊªöÂä®
      while (distance < 0) {
        lastIndex--;
        if (!state.cachedHeight[lastIndex]) {
          state.cachedHeight[lastIndex] = lineHeight;
        }
        distance += state.cachedHeight[lastIndex];
      }
      if (lastIndex < 0) {
        index = 0;
        offset = 0;
      } else {
        index = lastIndex;
        offset = distance;
      }
      calculateScrollY();
      if (start > index - bufferSize) {
        // ËÆ°ÁÆóÁ¨¨‰∏Ä‰∏™ÊåÇËΩΩÂÖÉÁ¥†
        start = Math.max(0, index - bufferSize);
      }
    }
    updateVisibleData();
  };

  !fixedHeight && watch(updateId, calculateScrollY, { flush: 'post' });
  const handleRowMounted = () => {
    if (!isVirtual.value) return;
    updateId.value++;
  };
  watch(data, () => {
    reset();
    state.visibleData = [];
    state.cachedScrollY = [];
    state.cachedHeight = [];
    beforeScrollTop = 0;
    index = 0;
    offset = 0;
    start = 0;
    // revising = false;
    trs.clear();
    if (data.value.length <= threshold) {
      state.visibleData = data.value;
    } else {
      updateVisibleData();
    }

    container.value && (container.value.scrollTop = 0);
  });
  let mounted = false;
  const refreshContainer = () => {
    if (mounted) {
      visibleCount = Math.ceil(container.value.offsetHeight / lineHeight);
      updateVisibleData();
    }
  };
  onMounted(() => {
    if (!window || !window.IntersectionObserver) {
      return;
    }
    const ob = new window.IntersectionObserver((entries) => {
      const entry = entries[0];
      if (entry.isIntersecting || entry.intersectionRatio) {
        mounted = true;
        isVirtual.value && refreshContainer();
        ob.unobserve(container.value);
      }
    });
    container.value && ob.observe(container.value);
  });
  return {
    trs,
    scrollHeight,
    ...toRefs(state),
    translateY,
    handleScroll,
    handleRowMounted,
    refreshContainer,
    fixedHeight,
    calculateScrollY,
  };
};
export default useVirtualScroll;
