import { BaseTableCol, ThMap } from './types';
import { getColWidthAttr } from './utils';

/**
 * Â°´ÂÖÖÊú™ËÆæÁΩÆwidthÂ±ûÊÄßÁöÑÂàó
 * @param columns ÂΩìÂâçË°®Ê†ºÊâÄÊúâÂàó
 * @param missingWidthCols Êú™ËÆæÁΩÆwidthÂ±ûÊÄßÁöÑÂàó
 * @param thWidthList ÂàóÂÆΩËÆ∞ÂΩïÂ≠óÂÖ∏
 * @param tableLayout Ë°®Ê†ºÂ∏ÉÂ±Ä
 * @param actualWidth ÂêÑÂàóÂÆûÈôÖÊÄªÂàóÂÆΩ
 * @param tableWidth Ë°®Ê†ºÊÄªÂÆΩÂ∫¶
 */
const setMissingColumnWidth = <T extends BaseTableCol<T>>(
  columns: T[],
  missingWidthCols: T[],
  thWidthList: ThMap,
  tableLayout: string,
  actualWidth: number,
  tableWidth: number
) : void => {
  const thMap = thWidthList;
  // ÂΩìÂâçÂàóÂÆΩÊÄªÂÆΩÂ∫¶Â∞è‰∫éË°®ÂÆΩÔºåÂ∞ÜÂâ©‰ΩôÂÆΩÂ∫¶Âπ≥ÂùáÂàÜÈÖçÁªôÊú™ÊåáÂÆöÂÆΩÂ∫¶ÁöÑÂàó
  if (actualWidth < tableWidth) {
    let widthDiff = tableWidth - actualWidth;
    const remainCols: T[] = [];
    // ‰ºòÂÖà‰øùËØÅËÆæÁΩÆ‰∫ÜminWidthÁöÑÂàóÊª°Ë∂≥ÊúÄÂ∞èÂÆΩÂ∫¶
    missingWidthCols.forEach((col) => {
      const minWidth = getColWidthAttr(col, 'minWidth');
      if (minWidth) {
        thMap[col.colKey] = minWidth;
        widthDiff -= minWidth;
      } else {
        remainCols.push(col);
      }
    });

    // Â¶ÇÊûúÂâ©‰ΩôÂÆΩÂ∫¶ > 0
    if (widthDiff > 0) {
      // Â¶ÇÊûúÂ≠òÂú®Êú™ËÆæÁΩÆminWidthÁöÑÂàóÔºåËøô‰∫õÂàóÂùáÂàÜÂâ©‰ΩôÂÆΩÂ∫¶
      if (remainCols.length) {
        const avgWidth = widthDiff / remainCols.length;
        remainCols.forEach((col) => {
          thMap[col.colKey] = avgWidth;
        });
      } else {
        // Âê¶ÂàôÊâÄÊúâÂàóÂùáÂàÜÂâ©‰ΩôÂÆΩÂ∫¶
        const avgWidth = widthDiff / missingWidthCols.length;
        missingWidthCols.forEach((col) => {
          thMap[col.colKey] += avgWidth;
        });
      }
    } else {
      // Ââ©‰ΩôÂÆΩÂ∫¶ <= 0, ÊâÄÊúâÂâ©‰ΩôÂàóÈªòËÆ§Â°´ÂÖÖ100px
      remainCols.forEach((col) => {
        thMap[col.colKey] = 100;
      });
    }
  } else if (tableLayout === 'fixed') {
    // ÂΩìÂâçÂàóË°®ÊÄªÂÆΩÂ∫¶Â§ß‰∫éÁ≠â‰∫éË°®ÂÆΩÔºå‰∏îÂΩìÂâçÊéíÁâàÊ®°Âºè‰∏∫fixedÔºåÈªòËÆ§Â°´ÂÖÖminWidth || 100px
    missingWidthCols.forEach((col) => {
      thMap[col.colKey] = getColWidthAttr(col, 'minWidth') || 100;
    });
  } else {
    // ÂΩìÂâçÂàóË°®ÊÄªÂÆΩÂ∫¶Â§ß‰∫éÁ≠â‰∫éË°®ÂÆΩÔºå‰∏îÂΩìÂâçÊéíÁâàÊ®°Âºè‰∏∫aut
    // ÈªòËÆ§Â°´ÂÖÖminWidth || 100pxÔºåÁÑ∂ÂêéÊåâÊØî‰æãÈáçÊñ∞ÂàÜÈÖçÂêÑÂàóÂÆΩÂ∫¶
    let extraWidth = 0;
    missingWidthCols.forEach((col) => {
      extraWidth += getColWidthAttr(col, 'minWidth') || 100;
    });
    const totalWidth = extraWidth + actualWidth;
    columns.forEach((col) => {
      if (!thMap[col.colKey]) {
        const colWidth = getColWidthAttr(col, 'minWidth') || 100;
        thMap[col.colKey] = (colWidth / totalWidth) * tableWidth;
      } else {
        thMap[col.colKey] = (thMap[col.colKey] / totalWidth) * tableWidth;
      }
    });
  }
};

/**
 * ËÆæÁΩÆÊâÄÊúâÂàóÁöÑÂÆΩÂ∫¶
 * @param columns ÂΩìÂâçË°®Ê†ºÊâÄÊúâÂàó
 * @param thWidthList ÂàóÂÆΩËÆ∞ÂΩïÂ≠óÂÖ∏
 * @param actualWidth ÂêÑÂàóÂÆûÈôÖÊÄªÂàóÂÆΩ
 * @param tableWidth Ë°®Ê†ºÊÄªÂÆΩÂ∫¶
 * @param notCalculateWidthCols ‰∏çÈúÄË¶ÅÂèÇ‰∏éËÆ°ÁÆóÁöÑÂàóid
 */
const setNormalColumnWidth = <T extends BaseTableCol<T>>(
  columns: T[],
  thWidthList: ThMap,
  actualWidth: number,
  tableWidth: number,
  notCalculateWidthCols: string[]
) : void => {
  const thMap = thWidthList;
  columns.forEach((col) => {
    if (notCalculateWidthCols.includes(col.colKey)) return;
    thMap[col.colKey] = (thMap[col.colKey] / actualWidth) * tableWidth;
  });
};

/**
 * Ë°®Ê†ºÊú™ÂàùÂßãÂåñÊó∂ÈªòËÆ§Â°´ÂÖÖÂêÑÂàóÂÆΩÂ∫¶
 * @param missingWidthCols Êú™ËÆæÁΩÆwidthÂ±ûÊÄßÁöÑÂàó
 * @param thWidthList ÂàóÂÆΩËÆ∞ÂΩïÂ≠óÂÖ∏
 */
const setInitialColumnWidth = <T extends BaseTableCol<T>>(
  missingWidthCols: T[],
  thWidthList: { [colKey: string]: number },
) : void => {
  const thMap = thWidthList;
  // Ë°®Ê†ºÂÆΩÂ∫¶Êú™ÂàùÂßãÂåñÔºåÈªòËÆ§Â°´ÂÖÖminWidth || 100px
  missingWidthCols.forEach((col) => {
    thMap[col.colKey] = getColWidthAttr(col, 'minWidth') || 100;
  });
};

/**
 * ÈáçÊñ∞ÊåâËßÑÂàôÂàÜÈÖçÂêÑÂàóÂÆΩÂ∫¶
 * @param columns ÂΩìÂâçË°®Ê†ºÊâÄÊúâÂàó
 * @param thWidthList ÂàóÂÆΩËÆ∞ÂΩïÂ≠óÂÖ∏
 * @param tableLayout Ë°®Ê†ºÂ∏ÉÂ±Ä
 * @param tableElmWidth Ë°®Ê†ºÂÆΩÂ∫¶
 * @param notCalculateWidthCols ‰∏çÈúÄË¶ÅÂèÇ‰∏éËÆ°ÁÆóÁöÑÂàó
 * @param callback ÂõûË∞ÉÂáΩÊï∞
 */
export default function recalculateColumnWidth<T extends BaseTableCol<T>>(
  columns: T[],
  thWidthList: ThMap,
  tableLayout: string,
  tableElmWidth: number,
  notCalculateWidthCols: string[],
  callback: (widthMap: { [colKey: string]: number }) => void
): void {
  let actualWidth = 0;
  const missingWidthCols: T[] = [];
  const thMap: ThMap = {};

  // ËÆ°ÁÆóÁé∞ÊúâÂàóÁöÑÂàóÂÆΩÊÄªÂíå
  columns.forEach((col) => {
    if (!thWidthList[col.colKey]) {
      thMap[col.colKey] = getColWidthAttr(col, 'width');
    } else {
      thMap[col.colKey] = thWidthList[col.colKey];
    }
    const originWidth = thMap[col.colKey];
    if (originWidth) {
      actualWidth += originWidth;
    } else {
      missingWidthCols.push(col);
    }
  });

  let tableWidth = tableElmWidth;
  let needUpdate = false;
  // Ë°®ÂÆΩÊ≤°ÊúâÂàùÂßãÂåñÊó∂ÔºåÈªòËÆ§ÁªôÊ≤°ÊúâÊåáÂÆöÂàóÂÆΩÁöÑÂàóÊåáÂÆöÂÆΩÂ∫¶‰∏∫100px
  if (tableWidth > 0) {
    // Â≠òÂú®Ê≤°ÊúâÊåáÂÆöÂàóÂÆΩÁöÑÂàó
    if (missingWidthCols.length) {
      setMissingColumnWidth(
        columns,
        missingWidthCols,
        thMap,
        tableLayout,
        actualWidth,
        tableWidth
      );
      needUpdate = true;
    } else {
      // ÊâÄÊúâÂàóÈÉΩÂ∑≤ÁªèÊåáÂÆöÂÆΩÂ∫¶
      if (notCalculateWidthCols.length) {
        // Â≠òÂú®‰∏çÂÖÅËÆ∏ÈáçÊñ∞ËÆ°ÁÆóÂÆΩÂ∫¶ÁöÑÂàóÔºà‰∏ÄËà¨ÊòØresizeÂêéÁöÑ‰∏§ÂàóÔºâÔºåËøô‰∫õÂàó‰∏çÂèÇ‰∏éÂêéÁª≠ËÆ°ÁÆó
        let sum = 0;
        notCalculateWidthCols.forEach((colKey) => {
          sum += thMap[colKey];
        });
        actualWidth -= sum;
        tableWidth -= sum;
      }
      // ÈáçÊñ∞ËÆ°ÁÆóÂÖ∂‰ªñÂàóÁöÑÂÆΩÂ∫¶ÔºåÊåâË°®Ê†ºÂâ©‰ΩôÂÆΩÂ∫¶ËøõË°åÊåâÊØî‰æãÂàÜÈÖç
      if (actualWidth !== tableWidth || notCalculateWidthCols.length) {
        setNormalColumnWidth(
          columns,
          thMap,
          actualWidth,
          tableWidth,
          notCalculateWidthCols
        );
        needUpdate = true;
      }
    }
  } else {
    setInitialColumnWidth(missingWidthCols, thMap);
    needUpdate = true;
  }

  // ÂàóÂÆΩËΩ¨‰∏∫Êï¥Êï∞
  if (needUpdate) {
    let addon = 0;
    Object.keys(thMap).forEach((key) => {
      const width = thMap[key];
      addon += width - Math.floor(width);
      thMap[key] = Math.floor(width) + (addon > 1 ? 1 : 0);
      if (addon > 1) {
        addon -= 1;
      }
    });
    if (addon > 0.5) {
      thMap[columns[0].colKey] += 1;
    }
  }

  // ÂõûË∞ÉÂ§ÑÁêÜ
  callback(thMap);
}
