  /** Á°ÆËÆ§ÊåâÈíÆ„ÄÇÂÄºÁ±ªÂûã‰∏∫Â≠óÁ¨¶‰∏≤ÔºåÂàôË°®Á§∫Ëá™ÂÆö‰πâÊåâÈíÆÊñáÊú¨ÔºåÂÄºÁ±ªÂûã‰∏∫ Object ÂàôË°®Á§∫ÈÄè‰º† Button ÁªÑ‰ª∂Â±ûÊÄß„ÄÇ‰ΩøÁî® TNode Ëá™ÂÆö‰πâÊåâÈíÆÊó∂ÔºåÈúÄËá™Ë°åÊéßÂà∂Á°ÆËÆ§‰∫ã‰ª∂ */
