import { TNode } from '../../common';
/**
 * ËÆ°ÁÆóÂàªÂ∫¶Âå∫Èó¥ÂÄºÂÅúÊ≠¢ÂùêÊ†á
 * @param position ÂàªÂ∫¶ÂùêÊ†áÂÄº ;
 * @param isVertical ÊòØÂê¶ÂûÇÁõ¥ÊñπÂêë
 * @returns {string} styleÂÜÖËÅîÊ†∑ÂºèÂÄº
 */
export const getStopStyle = (position: number, isVertical: boolean) => {
  return isVertical ? { top: `calc(${100 - position}% - 1px)` } : { left: `${position}%` };
};

/**
 * Ê†ºÂºèÂåñËøîÂõûsliderÂàùÂßãÂÄº
 * @param val slider‰º†ÂÖ•ÁöÑvalue
 * @param type Á¨¨‰∏Ä‰∏™ÊªëÂùóorÁ¨¨‰∫å‰∏™ÊªëÂùóÂÄº
 * @returns {number}
 */
export const formatSliderValue = (val: number | number[], type: 'first' | 'second') => {
  if (type === 'first') {
    if (val instanceof Array) {
      return val[0];
    }
    return val;
  }
  if (val instanceof Array) {
    return val[1];
  }
  return 0;
};

/**
 * Ê†ºÂºèÂåñlabelÂèÇÊï∞
 * @param label slider‰º†ÂÖ•ÁöÑlabelÂ±ûÊÄß
 * @param val slider‰º†ÂÖ•ÁöÑvalue
 */
export const formatLabel = (label: TNode | string, val: number) => {
  if (Boolean(label) === false) {
    return String(val);
  }
  if (typeof label === 'string') {
    let text = String(val);
    try {
      const rule = /\${value}%/g;
      const enableToReplace = rule.test(label);
      if (enableToReplace) {
        text = label.replace(rule, String(val));
      } else {
        text = label;
        throw new Error();
      }
    } catch (e) {
      console.warn(`fail to parse label prop, please pass string such as '\${value}%'`);
    }
    return text;
  }
  return () => label;
};
