import { computed, nextTick } from 'vue';
import dayjs from 'dayjs';
import { COMPONENT_NAME } from '../const';
import { CalendarState } from '../interface';
import { TdCalendarProps, ControllerOptions } from '../type';
import { useConfig } from '../../hooks/useConfig';

const getDefaultControllerConfigData = (visible = true): Record<string, any> => ({
  visible, // ÊòØÂê¶ÊòæÁ§∫ÔºàÂÖ®ÈÉ®Êéß‰ª∂Ôºâ
  disabled: false, // ÊòØÂê¶Á¶ÅÁî®ÔºàÂÖ®ÈÉ®Êéß‰ª∂Ôºâ
  // Ê®°ÂºèÂàáÊç¢ÂçïÈÄâÁªÑ‰ª∂ËÆæÁΩÆ
  mode: {
    visible: true, // ÊòØÂê¶ÊòæÁ§∫
    radioGroupProps: {}, // Áî®‰∫éÈÄè‰º†propsÁªôËØ•radioGroupÁªÑ‰ª∂
  },
  // Âπ¥‰ªΩÈÄâÊã©Ê°ÜÁªÑ‰ª∂Áõ∏ÂÖ≥ËÆæÁΩÆ
  year: {
    visible: true, // ÊòØÂê¶ÊòæÁ§∫
    selectProps: {}, // Áî®‰∫éÈÄè‰º†propsÁªôËØ•selectÁªÑ‰ª∂
  },
  // Âπ¥‰ªΩÈÄâÊã©Ê°ÜÁªÑ‰ª∂Áõ∏ÂÖ≥ËÆæÁΩÆ
  month: {
    visible: true, // ÊòØÂê¶ÊòæÁ§∫Ôºà‚Äúyear‚ÄùÊ®°Âºè‰∏ãÊú¨Ë∫´ÊòØ‰∏çÊòæÁ§∫ËØ•ÁªÑ‰ª∂ÁöÑÔºâ
    selectProps: {}, // Áî®‰∫éÈÄè‰º†propsÁªôËØ•selectÁªÑ‰ª∂
  },
  // ÈöêËóè\ÊòæÁ§∫Âë®Êú´ÊåâÈíÆÁªÑ‰ª∂Áõ∏ÂÖ≥ËÆæÁΩÆ
  weekend: {
    visible: true, // ÊòØÂê¶ÊòæÁ§∫
    showWeekendButtonProps: {}, // Áî®‰∫éÈÄè‰º†propsÁªôÊòæÁ§∫Âë®Êú´ÊåâÈíÆÁªÑ‰ª∂
    hideWeekendButtonProps: {}, // Áî®‰∫éÈÄè‰º†propsÁªôÈöêËóèÂë®Êú´ÊåâÈíÆÁªÑ‰ª∂
  },
  // ‚Äú‰ªäÂ§©\Êú¨Êúà‚ÄùÊåâÈíÆÁªÑ‰ª∂Áõ∏ÂÖ≥ËÆæÁΩÆ
  current: {
    visible: true, // ÊòØÂê¶ÊòæÁ§∫
    currentDayButtonProps: {}, // Áî®‰∫éÈÄè‰º†propsÁªô‚Äú‰ªäÂ§©‚ÄùÈíÆÁªÑ‰ª∂Ôºà‚Äúmonth‚ÄùÊ®°Âºè‰∏ãÊúâÊïàÔºâ
    currentMonthButtonProps: {}, // Áî®‰∫éÈÄè‰º†propsÁªô‚ÄúÊú¨Êúà‚ÄùÊåâÈíÆÁªÑ‰ª∂Ôºà‚Äúyear‚ÄùÊ®°Âºè‰∏ãÊúâÊïàÔºâ
  },
});

export function userController(props: TdCalendarProps, state: CalendarState) {
  const { globalConfig } = useConfig(COMPONENT_NAME);

  const options = computed<ControllerOptions>(() => {
    const dayJsFilterDate = dayjs(`${state.curSelectedYear}-${state.curSelectedMonth}`);
    const re = {
      isShowWeekend: state.isShowWeekend,
      filterDate: dayJsFilterDate.toDate(),
      formattedFilterDate: dayJsFilterDate.format(props.format),
      mode: state.curSelectedMode,
    };
    return re;
  });
  const configData = computed<Record<string, any>>(() => {
    const controllerConfig = props.controllerConfig ?? globalConfig.value.controllerConfig ?? true;
    if (typeof controllerConfig === 'boolean') {
      return getDefaultControllerConfigData(controllerConfig);
    }
    return {
      ...getDefaultControllerConfigData(),
      ...controllerConfig,
    };
  });
  const visible = computed<boolean>(() => {
    return !!configData.value && configData.value.visible;
  });
  /**
   * Âà§Êñ≠Êüê‰∏™Êéß‰ª∂ÊòØÂê¶ÊòæÁ§∫
   * @param name Êéß‰ª∂ÂØπÂ∫îÁöÑÈÖçÁΩÆËäÇÁÇπÂêçÔºàÂØπÂ∫îcontrollerConfigDataÔºâ
   * @returns trueË°®Á§∫ÊòæÁ§∫
   */
  function checkControllerVisible(name: string): boolean {
    let re = true;
    const conf = configData.value;
    if (!conf || !conf.visible || conf[name] === false || (conf[name] && !conf[name].visible)) {
      re = false;
    }
    return re;
  }
  /**
   * Âà§Êñ≠Êüê‰∏™Êéß‰ª∂ÊòØÂê¶Á¶ÅÁî®
   * @param name Êéß‰ª∂ÂØπÂ∫îÁöÑÈÖçÁΩÆËäÇÁÇπÂêçÔºàÂØπÂ∫îcontrollerConfigDataÔºâ
   * @param propsName Ë°®Á§∫Á¶ÅÁî®ÁöÑÊéß‰ª∂Â±ûÊÄßÔºàÂØπÂ∫îcontrollerConfigDataÔºâ
   * @returns trueË°®Á§∫Á¶ÅÁî®
   */
  function checkControllerDisabled(name: string, propsName: string): boolean {
    let re = false;
    const conf = configData.value;
    if (conf && (conf.disabled || (conf[name] && conf[name][propsName] && conf[name][propsName].disabled))) {
      re = true;
    }
    return re;
  }
  function emitControllerChange() {
    nextTick(() => {
      typeof props.onControllerChange === 'function' && props.onControllerChange({ ...options.value });
    });
  }

  return {
    options,
    configData,
    visible,
    checkControllerVisible,
    checkControllerDisabled,
    emitControllerChange,
  };
}
