import { ref, provide, onUpdated } from 'vue';

export const TDisplayNoneElementRefresh = 't-display-none-element-refresh';

// destroyOnClose=false Êó∂ÔºåÁà∂ÂÖÉÁ¥†‰∏∫ display: noneÔºåÊ≠§Êó∂ÁöÑÂ≠êÂÖÉÁ¥†Êó†Ê≥ïËé∑ÂèñÂà∞Ëá™Ë∫´ÂÖÉÁ¥†ÁöÑ‰ªª‰ΩïÂÆΩÂ∫¶
// Âõ†Ê≠§ÔºåÈúÄÂú®Áà∂ÂÖÉÁ¥† display: none ÂèëÁîüÂèòÂåñÊó∂‰∏ªÂä®Êõ¥Êñ∞Â≠êÂÖÉÁ¥†
export default function useDestroyOnClose() {
  const refresh = ref(0);
  provide(TDisplayNoneElementRefresh, refresh);
  onUpdated(() => {
    refresh.value += 1;
  });
}
