import { h, getCurrentInstance, ComponentInternalInstance, VNode } from 'vue';
import isFunction from 'lodash/isFunction';
import camelCase from 'lodash/camelCase';
import kebabCase from 'lodash/kebabCase';
import { getDefaultNode, getParams, OptionsType, JSXRenderContext } from '../utils/render-tnode';

// ÂÖºÂÆπÂ§ÑÁêÜÊèíÊßΩÂêçÁß∞ÔºåÂêåÊó∂ÊîØÊåÅÈ©ºÂ≥∞ÂëΩÂêçÂíå‰∏≠ÂàíÁ∫øÂëΩÂêçÔºåÁ§∫‰æãÔºövalue-display Âíå valueDisplay
function handleSlots(instance: ComponentInternalInstance, name: string, params: Record<string, any>) {
  // Ê£ÄÊü•ÊòØÂê¶Â≠òÂú® È©ºÂ≥∞ÂëΩÂêç ÁöÑÊèíÊßΩ
  let node = instance.slots[camelCase(name)]?.(params);
  if (node) return node;
  // Ê£ÄÊü•ÊòØÂê¶Â≠òÂú® ‰∏≠ÂàíÁ∫øÂëΩÂêç ÁöÑÊèíÊßΩ
  node = instance.slots[kebabCase(name)]?.(params);
  if (node) return node;
  return null;
}

/**
 * ÊòØÂê¶‰∏∫Á©∫ËäÇÁÇπÔºåÈúÄË¶ÅËøáÊª§ÊéâÊ≥®ÈáäËäÇÁÇπ„ÄÇÊ≥®ÈáäËäÇÁÇπ‰πü‰ºöË¢´ËÆ§‰∏∫ÊòØÁ©∫ËäÇÁÇπ
 */
function isEmptyNode(node: any) {
  if ([undefined, null, ''].includes(node)) return true;
  const innerNodes = node instanceof Array ? node : [node];
  const r = innerNodes.filter((node) => node?.type?.toString() !== 'Symbol(Comment)');
  return !r.length;
}

/**
 * ÈÄöËøá JSX ÁöÑÊñπÂºèÊ∏≤Êüì TNodeÔºåprops Âíå ÊèíÊßΩÂêåÊó∂Â§ÑÁêÜÔºå‰πüËÉΩÂ§ÑÁêÜÈªòËÆ§ÂÄº‰∏∫ true ÂàôÊ∏≤ÊüìÈªòËÆ§ËäÇÁÇπÁöÑÊÉÖÂÜµ
 * ‰ºòÂÖàÁ∫ßÔºöProps Â§ß‰∫éÊèíÊßΩ
 * Â¶ÇÊûú props ÂÄº‰∏∫ true ÔºåÂàô‰ΩøÁî®ÊèíÊßΩÊ∏≤Êüì„ÄÇÂ¶ÇÊûú‰πüÊ≤°ÊúâÊèíÊßΩÁöÑÊÉÖÂÜµ‰∏ãÔºåÂàô‰ΩøÁî® defaultNode Ê∏≤Êüì
 * @example const renderTNodeJSX = useTNodeJSX()
 * @return () => {}
 * @param name ÊèíÊßΩÂíåÂ±ûÊÄßÂêçÁß∞
 * @param options ÂÄºÂèØËÉΩ‰∏∫ÈªòËÆ§Ê∏≤ÊüìËäÇÁÇπÔºå‰πüÂèØËÉΩÊòØÈªòËÆ§Ê∏≤ÊüìËäÇÁÇπÂíåÂèÇÊï∞ÁöÑÈõÜÂêà
 * @example renderTNodeJSX('closeBtn')  ‰ºòÂÖàÁ∫ß props function Â§ß‰∫é ÊèíÊßΩ
 * @example renderTNodeJSX('closeBtn', <close-icon />)„ÄÇ ÂΩìÂ±ûÊÄßÂÄº‰∏∫ true Êó∂ÂàôÊ∏≤Êüì <close-icon />
 * @example renderTNodeJSX('closeBtn', { defaultNode: <close-icon />, params })„ÄÇ params ‰∏∫Ê∏≤ÊüìËäÇÁÇπÊó∂ÊâÄÈúÄÁöÑÂèÇÊï∞
 */
export const useTNodeJSX = () => {
  const instance = getCurrentInstance();
  return function (name: string, options?: OptionsType) {
    // assemble params && defaultNode
    const params = getParams(options);
    const defaultNode = getDefaultNode(options);

    // Â§ÑÁêÜ props Á±ªÂûãÁöÑNode
    let propsNode;
    if (Object.keys(instance.props).includes(name)) {
      propsNode = instance.props[name];
    }

    // ÊòØÂê¶ÈùôÈªòÊó•Âøó
    // const isSilent = Boolean(isObject(options) && 'silent' in options && options.silent);
    // // ÂêåÂêçÊèíÊßΩÂíåÂ±ûÊÄßÂêåÊó∂Â≠òÂú®ÔºåÂàôÊèêÈÜíÁî®Êà∑Âè™ÈúÄË¶ÅÈÄâÊã©‰∏ÄÁßçÊñπÂºèÂç≥ÂèØ
    // if (instance.slots[name] && propsNode && propsNode !== true && !isSilent) {
    //   log.warn('', `Both slots.${name} and props.${name} exist, props.${name} is preferred`);
    // }
    // propsNode ‰∏∫ false ‰∏çÊ∏≤Êüì
    if (propsNode === false) return;
    if (propsNode === true) {
      return handleSlots(instance, name, params) || defaultNode;
    }

    // ÂêåÂêç props Âíå slot ‰ºòÂÖàÂ§ÑÁêÜ props
    if (isFunction(propsNode)) return propsNode(h, params);
    const isPropsEmpty = [undefined, params, ''].includes(propsNode);
    if (isPropsEmpty && (instance.slots[camelCase(name)] || instance.slots[kebabCase(name)])) {
      return handleSlots(instance, name, params);
    }
    return propsNode;
  };
};

/**
 * Âú®setup‰∏≠ÔºåÈÄöËøáJSXÁöÑÊñπÂºè TNodeÔºåprops Âíå ÊèíÊßΩÂêåÊó∂Â§ÑÁêÜ„ÄÇ‰∏é renderTNodeJSX Âå∫Âà´Âú®‰∫éÂ±ûÊÄßÂÄº‰∏∫ undefined Êó∂‰ºöÊ∏≤ÊüìÈªòËÆ§ËäÇÁÇπ
 * @example const renderTNodeJSXDefault = useTNodeDefault()
 * @return () => {}
 * @param name ÊèíÊßΩÂíåÂ±ûÊÄßÂêçÁß∞
 * @example renderTNodeJSXDefault('closeBtn')
 * @example renderTNodeJSXDefault('closeBtn', <close-icon />) closeBtn ‰∏∫Á©∫Êó∂ÔºåÂàôÂÖúÂ∫ïÊ∏≤Êüì <close-icon />
 * @example renderTNodeJSXDefault('closeBtn', { defaultNode: <close-icon />, params }) „ÄÇparams ‰∏∫Ê∏≤ÊüìËäÇÁÇπÊó∂ÊâÄÈúÄÁöÑÂèÇÊï∞
 */
export const useTNodeDefault = () => {
  const renderTNodeJSX = useTNodeJSX();
  return function (name: string, options?: VNode | JSXRenderContext) {
    const defaultNode = getDefaultNode(options);
    return renderTNodeJSX(name, options) || defaultNode;
  };
};

/**
 * Âú®setup‰∏≠ÔºåÁî®‰∫éÂ§ÑÁêÜÁõ∏ÂêåÂêçÁß∞ÁöÑ TNode Ê∏≤Êüì
 * @example const renderContent = useContent()
 * @return () => {}
 * @param name1 Á¨¨‰∏Ä‰∏™ÂêçÁß∞Ôºå‰ºòÂÖàÁ∫ßÈ´ò‰∫é name2
 * @param name2 Á¨¨‰∫å‰∏™ÂêçÁß∞
 * @param defaultNode ÈªòËÆ§Ê∏≤ÊüìÂÜÖÂÆπÔºöÂΩì name1 Âíå name2 ÈÉΩ‰∏∫Á©∫Êó∂‰ºöÂêØÂä®ÈªòËÆ§ÂÜÖÂÆπÊ∏≤Êüì
 * @example renderContent('default', 'content')
 * @example renderContent('default', 'content', 'ÊàëÊòØÈªòËÆ§ÂÜÖÂÆπ')
 * @example renderContent('default', 'content', { defaultNode: 'ÊàëÊòØÈªòËÆ§ÂÜÖÂÆπ', params })
 */
export const useContent = () => {
  const renderTNodeJSX = useTNodeJSX();
  return function (name1: string, name2: string, options?: VNode | JSXRenderContext) {
    // assemble params && defaultNode
    const params = getParams(options);
    const defaultNode = getDefaultNode(options);

    const toParams = params ? { params } : undefined;

    const node1 = renderTNodeJSX(name1, toParams);
    const node2 = renderTNodeJSX(name2, toParams);

    const res = isEmptyNode(node1) ? node2 : node1;
    return isEmptyNode(res) ? defaultNode : res;
  };
};
