import dayjs from 'dayjs';

import { CalendarCell } from './type';

// ÁªÑ‰ª∂ÁöÑ‰∏Ä‰∫õÂ∏∏Èáè
import { FIRST_MONTH_OF_YEAR, LAST_MONTH_OF_YEAR, DAY_CN_MAP } from './const';

/**
 * Ëé∑Âèñ‰∏Ä‰∏™Êó•ÊúüÊòØÂë®Âá†Ôºà1~7Ôºâ
 */
export const getDay = (dt: Date): number => {
  let day = dayjs(dt).day();
  if (day === 0) {
    day = 7;
  }
  return day;
};

/**
 * Ëé∑ÂèñÊòüÊúüÁöÑ‰∏≠Êñá
 * @param num ÊòüÊúüÔºà1~7Ôºâ
 */
export const getDayCn = (num: number): string => {
  let re = '';
  const numStr = num.toString();
  if (numStr in DAY_CN_MAP) {
    re = DAY_CN_MAP[numStr];
  }
  return re;
};

/**
 * Ëé∑Âèñ‰∏Ä‰∏™Êó•ÊúüÂú®Êó•ÂéÜ‰∏äÁöÑÂàó‰∏ãÊ†á
 * @param firstDayOfWeek Âë®Ëµ∑ÂßãÊó•Ôºà1~7Ôºâ
 * @param dt
 */
export const getCellColIndex = (firstDayOfWeek: number, dt: Date): number => {
  let re = 0;
  const day = getDay(dt);
  if (day >= firstDayOfWeek) {
    re = day - firstDayOfWeek;
  } else {
    re = 7 - firstDayOfWeek + day;
  }
  return re;
};
/**
 * ËøîÂõûÊó•Êúü+Â§©Êï∞ÔºàÂ§©Êï∞ÂèØ‰ª•Ë¥üÊï∞Ôºâ
 */
export const addDate = (dt: Date, days: number) => {
  const d = new Date(dt);
  d.setDate(d.getDate() + days);
  return d;
};

/**
 * ÂàõÂª∫ÊúàÂéÜÂçïÂÖÉÊ†ºÊï∞ÊçÆ
 * @param year ÊúàÂéÜÂπ¥‰ªΩ
 * @param curDate ÂΩìÂâçÊó•Êúü
 */
export const createYearCellsData = (year: number, curDate: dayjs.Dayjs, format: string): CalendarCell[] => {
  const monthsArr: CalendarCell[] = [];
  const isCurYear = curDate.year() === year;
  for (let num = FIRST_MONTH_OF_YEAR; num <= LAST_MONTH_OF_YEAR; num++) {
    const date = new Date(year, num - 1);
    const curDateMon = parseInt(curDate.format('M'), 10);
    const isCurrent = isCurYear && curDateMon === num;
    monthsArr.push({
      mode: 'year',
      isCurrent,
      date,
      formattedDate: dayjs(date).format(format),
      filterDate: null,
      formattedFilterDate: null,
      isShowWeekend: true,
    });
  }

  return monthsArr;
};

/**
 * ÂàõÂª∫Êó•ÂéÜÂçïÂÖÉÊ†ºÊï∞ÊçÆ
 * @param year Êó•ÂéÜÂπ¥‰ªΩ
 * @param month Êó•ÂéÜÊúà‰ªΩ
 * @param firstDayOfWeek Âë®Ëµ∑ÂßãÊó•Ôºà1~7Ôºâ
 * @param curDate ÂΩìÂâçÊó•Êúü
 * @param format Êó•ÊúüÊ†ºÂºè
 */
export const createMonthCellsData = (
  year: number,
  month: number,
  firstDayOfWeek: number,
  curDate: dayjs.Dayjs,
  format: string,
): CalendarCell[][] => {
  const daysArr: CalendarCell[][] = [];
  // ÂΩìÂâçÊúà‰ªΩÁöÑÂºÄÂßãÊó•Êúü
  const begin: Date = dayjs(`${year}-${month}`).startOf('month').toDate();
  // ÂΩìÂâçÊúà‰ªΩÁöÑÁªìÊùüÊó•Êúü
  const end: Date = dayjs(`${year}-${month}`).endOf('month').toDate();
  const days = end.getDate();

  const beginDateColIndex = getCellColIndex(firstDayOfWeek, begin);
  let arr = [];
  let num = 1;

  const createCellData = (belongTo: number, isCurrent: boolean, date: Date, weekOrder: number): CalendarCell => {
    const day = getDay(date);
    return {
      mode: 'month',
      belongTo,
      isCurrent,
      day,
      weekOrder,
      date,
      formattedDate: dayjs(date).format(format),
      filterDate: null,
      formattedFilterDate: null,
      isShowWeekend: true,
    };
  };

  // Ê∑ªÂä†‰∏ä‰∏™Êúà‰∏≠ÂíåÂΩìÂâçÊúàÁ¨¨‰∏ÄÂ§©Âêå‰∏ÄÂë®ÁöÑÊó•Êúü
  for (let i = 0; i < beginDateColIndex; i++) {
    const date = addDate(begin, i - beginDateColIndex);
    arr.push(createCellData(-1, false, date, num));
    if (arr.length === 7) {
      daysArr.push(arr);
      arr = [];
      num += 1;
    }
  }
  for (let i = 0; i < days; i++) {
    const date = addDate(begin, i);
    arr.push(createCellData(0, curDate.isSame(dayjs(date)), date, num));
    if (arr.length === 7) {
      daysArr.push(arr);
      arr = [];
      num += 1;
    }
  }
  // Ê∑ªÂä†‰∏ã‰∏™Êúà‰∏≠ÂíåÂΩìÂâçÊúàÊúÄÂêéÂêå‰∏ÄÂë®ÁöÑÊó•Êúü
  if (arr.length) {
    const nextMonthCellNum = 7 - arr.length;
    for (let i = 0; i < nextMonthCellNum; i++) {
      const date = addDate(end, i + 1);
      arr.push(createCellData(1, false, date, num));
    }
    daysArr.push(arr);
  }

  return daysArr;
};

/**
 * Ê†πÊçÆÂΩìÂâçÊó∂Èó¥ÂàõÂª∫‰∏Ä‰∏™ÈªòËÆ§Êó•Êúü
 * @returns ÂΩìÂâçÊó•ÊúüÈÉΩdayjsÂØπË±°
 */
export const createDefaultCurDate = (): dayjs.Dayjs => dayjs(dayjs().format('YYYY-MM-DD'));
